import 'react-tabs/style/react-tabs.css';
import './app.css';

import React from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import {
    Map, Totals, Correlation, DataProvider, DataConsumer, DateSlider
} from 'components'

const Loading = () => <label className="loading-indicator" >Loading ...</label>

const render = (loaded) =>
    loaded ? (
        <section role="main">
            <DateSlider />
            <Tabs>
                <TabList>
                    <Tab>Map</Tab>
                    <Tab>Totals</Tab>
                    <Tab>Correlation</Tab>
                </TabList>
                <TabPanel>
                    <Map />
                </TabPanel>
                <TabPanel>
                    <Totals />
                </TabPanel>
                <TabPanel>
                    <Correlation />
                </TabPanel>
            </Tabs>
        </section>
    ):
    (<Loading />)

const App =() => (
    <DataProvider>
        <header>
            <h1>NSW Australia Corona virus spread analysis</h1>
            <p>The information presented is for demonstration purposes only and not for research
                or any other purposes. No part of these pages, either texts or images may be used
                for any purpose other than personal use
            </p>
        </header>
        <DataConsumer>
            {(data) => render(data.loaded)}
        </DataConsumer>
    </DataProvider>
);

export default App;
